import React from 'react';
import { useRecoilValue } from 'recoil';
import { motion } from 'framer-motion';
import { blockAnim } from '../utilities/animationVariants';
import * as AppState from '../AppState';
import './Nav.scss';


function scrollIntoView(id: string) {
  const yOffset = -78; 
  const target = document.getElementById(id);;
  const y = target ? target.getBoundingClientRect().top + window.pageYOffset + yOffset : 72;
  window.scrollTo({top: y, behavior: 'smooth'});
}

const Nav = () => {
  const PDF = process.env.PUBLIC_URL + '/download/David-A-George-Resume.pdf';

  const screenBelowSm = useRecoilValue(AppState.screenBelowSm);

  return (
    <nav role="navigation" className="w-100 px-2 px-lg-5 py-3">
      <motion.div variants={blockAnim} initial="hidden" animate="visible" className="d-flex w-100 container-lg align-items-center justify-content-between">
        <motion.h1 variants={blockAnim} role="link" className="nav__headline link link-blue-300 fs-4 mb-0" onClick={() => scrollIntoView('homepage')} tabIndex={0}>
          { screenBelowSm ? 'DG.' : 'David A. George' }
        </motion.h1>
        <motion.div variants={blockAnim}>
          <motion.span variants={blockAnim} role="link" tabIndex={0} className="nav__nav-link link ms-2 ms-sm-3" onClick={() => scrollIntoView('work')}>Work</motion.span>
          <motion.a variants={blockAnim} href={PDF} className="nav__nav-link link ms-2 ms-sm-3" rel='noopener noreferrer' target="_blank" download>Resume</motion.a>
          <motion.a variants={blockAnim} href="mailto:dav.a.george@gmail.com" className="ms-2 ms-sm-3">
            <button className="dg-btn bg-gradient-green-200 py-2 px-3" onClick={() => {}}>
              Let's chat
            </button>
          </motion.a>
        </motion.div>
      </motion.div>
    </nav>
  )
}

export default Nav;