import React, { ReactFragment } from "react";

import "./CSSBlob.scss";

const CSSBlob = (props: { message?: ReactFragment | string }) => {
  return (
    <div className="css-blob__wrapper">
      <div className="css-blob">
        {props.message && (
          <span className="css-blob__message">{props.message}</span>
        )}
      </div>
      <div className="css-blob__shadow" />
    </div>
  );
};

export default CSSBlob;
